import Vue from "vue";
import VueRouter from "vue-router";
import Info from '../views/Info.vue'
import Event from '../views/Event.vue'
import Preset from '../views/Preset.vue'
import Live from '../views/Live.vue'
import { EventBus, EVENT_SET_EVENT_TYPE } from "../utils/event-bus";

Vue.use(VueRouter);

const requireAuth = (to, from, next) => {
  next()
  EventBus.$emit(EVENT_SET_EVENT_TYPE, to.path)

  // if ((from.path === "/event/preset" && to.path === "/event/live")
  //   || (from.path === "/info" && to.path === "/event/preset")) {
  //   next()
  //   EventBus.$emit(EVENT_SET_EVENT_TYPE, to.path)
  // }
  // else {
  //   router.push('/')
  // }
}

const routes = [
  {
    path: "/",
    redirect: '/info'
  },
  {
    path: '/info',
    component: Info
  },
  {
    path: '/event',
    component: Event,
    children: [
      {
        path: 'preset',
        component: Preset,
        beforeEnter: requireAuth
      },
      {
        path: 'live',
        component: Live,
        beforeEnter: requireAuth
      }
    ]
  },
];

const router = new VueRouter({
  routes
});

export default router;
