export function loadAllData() {
  return firebase
    .database()
    .ref("/messages")
    .orderByChild("time")
    .once("value");
}

export function loadPreviousMessages(count) {
  return firebase
    .database()
    .ref("/messages")
    .orderByChild("time")
    .limitToLast(count)
    .once("value");
}

export function checkMessages(timestamp) {
  return firebase
    .database()
    .ref("/messages")
    .orderByChild("time")
    .startAt(timestamp + 1)
    .once("value");
}

export function addData(root, value) {
  const new_post_key = firebase
    .database()
    .ref("/" + root)
    .child("data")
    .push().key;
  const new_key = "/" + new_post_key;

  const updates = {};
  updates[root + "/" + new_key] = { ...value };

  return firebase
    .database()
    .ref()
    .update(updates, function(error) {
      return error;
    });
}

export function loadHlsUrl() {
  return firebase
    .database()
    .ref("/")
    .once("value")
    .then(function(snapshot) {
      const hls_url = (snapshot.val() && snapshot.val().hls_url) || "";
      return hls_url;
    });
}

export function updateHlsUrl(root, value) {
  const updates = {};
  updates[root] = value;

  return firebase
    .database()
    .ref()
    .update(updates, function(error) {
      return error;
    });
}
