<template>
  <div ref="bg" class="tab_bg">
    <ChattingList class="list"></ChattingList>
    <div class="shadow_up"></div>
    <ChattingInput class="input"></ChattingInput>
  </div>
</template>

<script>
import ChattingInput from "@/components/ChattingInput.vue";
import ChattingList from "@/components/ChattingList.vue";

export default {
  components: { ChattingInput, ChattingList },
  created() {
    this.$nextTick(() => {
      const y = this.$refs.bg.getBoundingClientRect().top;
      const height = window.innerHeight - y;
      console.log(y, height);
      this.$refs.bg.style.height = height - 50 + "px";
    });
  }
};
</script>

<style scoped>
.tab_bg {
  overflow-y: scroll;
  width: 100%;
}

.list {
  width: 100%;
}

.input {
  bottom: 0;
  position: fixed;
  width: 100%;
  height: 50px;
}

.shadow_up {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 50px;
  box-shadow: 0px 1px 10px lightgray;
}
</style>
