<template>
  <div class="root">
    <ul ref="rlist" class="chatting_list">
      <li v-for="msg in getMessages" :key="msg.index" class="msg_obj">
        <div :class="!!msg.isMine && msg_right">
          <div class="msg_top">
            {{ msg.user }}
          </div>
          <div class="msg_horizontal">
            <div class="msg_item" :class="msg.isMine ? msg_mine : msg_other">
              {{ msg.message }}
            </div>
            <div class="msg_time">
              {{ getFormatDate(msg.time) }}
            </div>
          </div>
        </div>
      </li>
    </ul>
    <img ref="logo" class="logo_img" src="../assets/Logo-white.png" />
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { EventBus, EVENT_UPDATE_LIST } from "../utils/event-bus";

export default {
  data() {
    return {
      msg_right: "msg_right",
      msg_mine: "msg_mine",
      msg_other: "msg_other"
    };
  },
  computed: {
    ...mapGetters(["getMessages"])
  },
  created() {
    EventBus.$on(EVENT_UPDATE_LIST, () => {
      this.scrollToBottom();
    });

    this.$nextTick(() => {
      const rlist = this.$refs.rlist;
      const h = window.innerHeight - rlist.getBoundingClientRect().y - 50;
      rlist.style.height = h + "px";

      const v_h = (window.innerWidth * 9) / 16;
      this.$refs.logo.style.marginTop = v_h + "px";
    });
  },
  mounted() {
    this.$nextTick(() => {
      this.scrollToBottom();
    });
  },
  watch: {
    getMessages() {
      console.log("add item");
      this.$nextTick(function() {
        this.scrollToBottom();
      });
    }
  },
  methods: {
    getFormatDate(timestamp) {
      const date = new Date(timestamp);
      return date.toLocaleString("en-US", {
        hour: "numeric",
        minute: "numeric",
        hour12: true
      });
    },
    scrollToBottom() {
      const rlist = this.$refs.rlist;
      rlist.scrollTop = rlist.scrollHeight;
    }
  }
};
</script>

<style scoped>
.root {
  background-color: #f5f6f1;
}

.chatting_list {
  width: 100%;
  overflow-y: scroll;
  list-style: none;
}

.msg_item {
  box-sizing: border-box;
  margin: 0px 0px 10px 0px;
  padding: 10px;
  width: fit-content;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  position: relative;
  height: 1%;
  font-size: 15px;
}

.msg_obj {
  margin-left: -15px;
}

.msg_top {
  margin-left: 5px;
  font-size: 15px;
  display: flex;
  color: #666666;
}

.msg_time {
  margin-left: 5px;
  color: #707070;
  font-weight: normal;
  font-size: 10px;
  display: flex;
  align-items: flex-end;
  margin-bottom: 10px;
}

.chat_text {
  font-size: 20px;
}

.msg_right {
  display: flex;
  justify-content: flex-end;
  text-align: right;
}

.msg_mine {
  background-color: #03dac5;
  color: #ffffffde;
}

.msg_other {
  background-color: #f0f1f5;
  color: #666666;
}

.root {
  padding-top: 1px;
}

.logo_img {
  position: fixed;
  margin: 0;
  top: 10px;
  right: 10px;
  height: 12vh;
  pointer-events: none;
}

.msg_horizontal {
  display: flex;
}
</style>
